import React, { Component } from 'react'
import API from './API';
import DoctorRowInDep from './DoctorRowInDep';

export class FollowupDoctorsList extends Component {
    state = {
        doctors: [],
        loaded: false
    }

    componentDidMount() {
        this._isMounted = true;
        API.getPatientInformation(localStorage.getItem('token')).then((patient)=>{
            API.getDoctorsOfPatient(patient).then((doctors)=>{
                if (this._isMounted) {
                    this.setState({doctors: doctors, loaded: true});
                }
            });
        });
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    render() {
        return (
            <div className="schedule pt-4 w-40 text-center m-auto">
                {this.state.loaded || (
                    <div className="d-flex justify-content-center">
                        <div className="spinner-border" role="status">
                            <span className="visually-hidden">Loading...</span>
                        </div>
                    </div>
                )}
                {this.state.loaded && (
                    <table className="table table-striped">
                        <thead><tr>
                            <th>Name</th>
                            <th>View Schedule</th>
                            <th>New Appointment</th>
                        </tr></thead>
                        <tbody>
                            {this.state.doctors.map((doctor)=>(
                                <DoctorRowInDep
                                    doctor={doctor}
                                />
                            ))}
                        </tbody>
                    </table>
                )}
            </div>
        )
    }
}

export default FollowupDoctorsList
